@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box}h1,h2,h3{color:#363636}body{font-family:"Amatic SC", cursive}h3,h4{font-weight:400;color:#646464}.App{transition:all 0.5s ease}.library-active{margin-left:30%}.song-container{min-height:60vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.song-container img{width:20%;border-radius:50%}.song-container h2{padding:3rem 1rem 1rem 1rem;font-size:3rem}.song-container h3{font-size:2rem}@media screen and (max-width: 768px){.song-container img{width:60%}}.player{min-height:20vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between}.time-control{width:50%;display:flex}.time-control input{width:100%;-webkit-appearance:none;-moz-appearance:none;background:transparent;cursor:pointer}.time-control p{padding:1rem}.play-control{display:flex;justify-content:space-between;align-items:center;padding:1rem;width:30%}.play-control svg{cursor:pointer}p{font-size:1.5rem}input[type="range"]:focus{outline:none}input[type="range"]::-webkit-slider-thumb{-webkit-appearance:none;height:16px;width:16px;background:black}input[type="range"]::-moz-range-thumb{-moz-appearance:none;height:16px;width:16px;background:transparent;border:transparent}.track{background:lightblue;width:100%;height:1rem;position:relative;border-radius:1rem;overflow:hidden}.animate-track{background:#ccc;width:100%;height:100%;position:absolute;top:0;left:0;transform:translateX(0%);pointer-events:none}@media screen and (max-width: 768px){.time-control{width:90%}.play-control{width:70%}}.library{position:fixed;top:0;left:0;width:20rem;height:100%;background:white;box-shadow:2px 2px 50px #a2a2a2;overflow:scroll;transform:translateX(-100%);transition:all 0.5s ease;opacity:0}.library h2{padding:2rem}.library-song{display:flex;align-items:center;padding:1rem 2rem 1rem 2rem;cursor:pointer;transition:background 0.5s ease}.library-song img{width:30%}.library-song:hover{background:gainsboro}.song-description{padding-left:1rem}.song-description h3{font-size:2rem}.song-description h4{font-size:1rem}.selected{background:#aeadad}.active-library{transform:translateX(0%);opacity:1}*{scrollbar-width:thin;scrollbar-color:rgba(155,155,155,0.5) transparent}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{background:transparent}*::-webkit-scrollbar-thumb{background-color:rgba(155,155,155,0.5);border-radius:20px;border:transparent}@media screen and (max-width: 768px){.library{width:100%}}nav{min-height:10vh;display:flex;justify-content:space-around;align-items:center}nav button{background:transparent;cursor:pointer;border:2px solid #414141;padding:0.5rem;transition:all 0.3s ease}nav button:hover{background:#414141;color:white}@media screen and (max-width: 768px){nav button{z-index:10}}

